@import "C:/Users/david/Portfolio/CS/davidgram/frontend/src/config/_variables.scss";
.footer{
  max-width: $max-page-width;
  width: 95%;
  margin: 55px auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  font-size: $small-font-size;
  text-transform: uppercase;
  flex-wrap: wrap;
  @include breakpoint("phone-tablet"){
    justify-content: center;
    max-width: 350px;
    margin-bottom: 25px;
  }
  .column{
    @include breakpoint("phone-tablet"){
      margin-bottom: 0;
    }
    .nav{
      .list{
        display: flex;
        flex-wrap: wrap;
        @include breakpoint("phone-tablet"){
          justify-content: center;
        }
        .list-item{
          margin-right: 15px;
          color: $dark-blue;
          cursor: pointer;
          @include breakpoint("phone-tablet") {
            margin-bottom: 15px;
          }
        }
      }
    }
    .copyright{
      color: $dark-gray;
    }
  }
}


